.Button {
  width: 100%;
  margin: 5px auto;
  text-transform: uppercase;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.7);
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.7);
  padding: 10px;
}

.Button:hover {
  cursor: pointer;
  -webkit-box-shadow: 0 5px 8px rgba(0, 0, 0, 0.7);
          box-shadow: 0 5px 8px rgba(0, 0, 0, 0.7);
}


